import styles from './Button.module.scss';

interface ContainedButtonProps {
  onClick?: () => void;
  isClickable?: boolean;
  classNames?: string;
  children: React.ReactNode;
  color?: string;
  type?: 'hollow';
  widthType?: 'wide';

  isSelected?: boolean;
  isHidden?: boolean;
}

const ContainedButton = ({
  onClick,
  isClickable,
  classNames,
  children,
  isHidden,
  color,
  type,
  widthType,
}: ContainedButtonProps) => {
  const onClickHandler = () => {
    if (typeof isClickable === 'undefined' || isClickable) onClick && onClick();
  };
  const buttonClassNames = `
      ${styles.containedButton}
      ${styles[type || '']}
      ${styles[widthType || '']}
      ${classNames}
      ${typeof isClickable === 'undefined' || isClickable ? styles.clickable : ''}
      ${typeof isHidden === 'undefined' || !isHidden ? '' : styles.hidden}
      ${color ? styles.green : ''}
    `;
  return (
    <button className={buttonClassNames} onClick={onClickHandler}>
      {children}
    </button>
  );
};

export default ContainedButton;
