import { MaterialProps } from './Material';
import { MaterialFragment } from '@graphql/generated/graphql';
import cn from 'classnames';
import styles from './MaterialGroup.module.scss';
import { Falsey } from 'lodash';
import { usePortalRef } from '@hooks';
import { useEffect, useMemo, useState } from 'react';
import { createPortal } from 'react-dom';
import { useLongPress } from 'use-long-press';
import { loadImage } from '@lib/img.util';
import testCrystalMaterial from './test-crystal-material';

export const MaterialCrystal = ({
  className,
  material,
  onMaterialSelected,
  selectedMaterialId,
}: MaterialProps) => {
  const crystal = testCrystalMaterial.find((m) => m.materialId === material.id) || {
    materialId: material.id,
    color: 'pink',
  };
  const crystalPromise = useMemo(() => {
    return loadImage(`/materials/crystals/${crystal.color}.png`);
  }, []);

  const [crystalImage, setCrystalImage] = useState<HTMLImageElement | null>(null);
  useEffect(() => {
    crystalPromise.then((img) => {
      setCrystalImage(img);
    });
  }, [crystalPromise]);

  const rootClassName = cn(styles.material, className, {
    [styles.selected]: material.id === selectedMaterialId,
  });
  const popupPortalRef = usePortalRef('popupContainer');
  const [showPopup, setShowPopup] = useState<boolean>(false);

  const mapImage = material?.attributes?.images?.find((i) => i?.mapType === 'map');

  const onClick = () => {
    if (material) {
      onMaterialSelected(material);
    }
  };

  const onLongPressBinder = useLongPress((event) => {
    setShowPopup(true);
  });

  const handleContextMenu = (event: React.MouseEvent<HTMLElement>) => {
    event.preventDefault();
  };

  if (!crystalImage) return null;

  return (
    <>
      <button
        className={rootClassName}
        onContextMenu={handleContextMenu}
        onClick={onClick}
        {...onLongPressBinder()}
        data-name={material.attributes?.name}
      >
        {mapImage && <img src={crystalImage.src} />}
      </button>
      {showPopup &&
        popupPortalRef &&
        createPortal(
          <div
            className={styles.popup}
            onClick={() => {
              setShowPopup(false);
            }}
          >
            <img
              height='300'
              width='300'
              src={crystalImage.src}
              alt=''
              onContextMenu={handleContextMenu}
            />
          </div>,
          popupPortalRef,
        )}
    </>
  );

  return <></>;
};
export default MaterialCrystal;
