import { useCustomiserStore } from '@store/customiser';
import cn from 'classnames';

import styles from './NavHeader.module.scss';
import {
  getNavItemArrayIndex,
  getNavTabItemCount,
  hasNextNavItem as hasNextNavItemChecker,
  isFinishedNavItem,
  isLastNavItem as isLastNavItemChecker,
} from '@lib/object';

import { FaAngleLeft, FaAngleRight } from 'react-icons/fa6';
import { IoMdClose, IoMdReturnLeft } from 'react-icons/io';
import { navItemNameDisplay } from '@lib/string';

export interface NavHeaderProps {
  className?: string;
  toggle: () => void;
  isOpen: boolean;
  isMinimized?: boolean;
  onClick?: () => void;
}

const NavHeader = ({ className, toggle, isOpen, isMinimized = false, onClick }: NavHeaderProps) => {
  const rootClassName = cn(styles.root, className);
  const { selectedNav, setSelectedNav, navTabs, texts } = useCustomiserStore();
  const editText = texts?.find((g) => g.edit);

  const navTabItemCount =
    typeof selectedNav?.index === 'number'
      ? getNavTabItemCount({
          navTabs,
          navItemIndex: selectedNav.index,
        })
      : 0;
  const navItemArrayIndex =
    typeof selectedNav?.index === 'number'
      ? getNavItemArrayIndex({
          navTabs,
          navItemIndex: selectedNav.index,
        })
      : 0;

  const isFirstNavItem = typeof selectedNav?.index === 'number' ? selectedNav?.index === 0 : false;
  const isLastNavItem =
    typeof selectedNav?.index === 'number'
      ? isLastNavItemChecker({
          navTabs,
          navItemIndex: selectedNav?.index,
        })
      : false;

  const prevHandler = () => {
    if (editText) return;
    if (typeof selectedNav?.index !== 'number' || selectedNav?.index <= 0) {
      return;
    }
    setSelectedNav(selectedNav.index - 1);
  };
  const nextHandler = () => {
    if (editText) return;
    if (typeof selectedNav?.index !== 'number') {
      return;
    }
    setSelectedNav(selectedNav.index + 1);
  };

  const closeHandler = () => {
    if (onClick) {
      onClick();
    }
  };

  if (isOpen) {
    return null;
  }

  const minimizeIconClassname = cn(styles.minimizeIcon, {
    [styles.minimized]: isMinimized,
  });

  return (
    <>
      <div id='NavHeader' className={rootClassName}>
        <div className={styles.topIcons}>
          <span className={styles.topIcons__icon} onClick={closeHandler}>
            {!editText && <IoMdReturnLeft />}
          </span>
        </div>
        <div className={styles.nav}>
          <span
            className={`${styles.navButton} ${isFirstNavItem || editText ? styles.hidden : ''}`}
            onClick={prevHandler}
          >
            <FaAngleLeft />
          </span>
          <div className={styles.title}>
            <span>
              {selectedNav?.required && <span className={styles.title__requiredIcon}>*</span>}
              {navItemNameDisplay({ selectedNav })}
            </span>
            <span className={styles.title__page}>
              {navItemArrayIndex + 1}/{navTabItemCount}
            </span>
          </div>
          <span
            className={`${styles.navButton} ${isLastNavItem || editText ? styles.hidden : ''}`}
            onClick={nextHandler}
          >
            <FaAngleRight />
          </span>
        </div>
      </div>
    </>
  );
};

export default NavHeader;
