import PillButton from '@components/ui/PillButton';
import { MaterialFragment } from '@graphql/generated/graphql';
import cn from 'classnames';
import { Material } from './Material';
import styles from './MaterialGroup.module.scss';
import { useMaterialGroupStore } from './MaterialGroupState';
import { Falsey } from 'lodash';
import testCrystalMaterial from './test-crystal-material';
import MaterialCrystal from './MaterialCrystal';

export interface MaterialTypeProps {
  className?: string;
  onMaterialSelected: (material: MaterialFragment) => void;
  hideColorGroups?: boolean;
  hideMaterialTitle?: boolean;
  selectedMaterialId?: Falsey | string;
}

export const MaterialType = ({
  className,
  onMaterialSelected,
  hideColorGroups,
  hideMaterialTitle,
  selectedMaterialId,
}: MaterialTypeProps) => {
  const rootClassName = cn(styles.materialType, className);

  const {
    materialTypes,
    filteredMaterials: getFilteredMaterials,
    materials,
  } = useMaterialGroupStore();

  let filteredMaterials = hideColorGroups ? materials : getFilteredMaterials();
  let filteredMaterialTypes = materialTypes;
  if (hideMaterialTitle) {
    filteredMaterials = filteredMaterials.filter((m) => {
      return testCrystalMaterial.some((acm) => acm.materialId === m.id);
    });
    filteredMaterialTypes = materialTypes.filter((mt) =>
      filteredMaterials.some((m) => m.attributes?.type?.data?.id === mt.id),
    );
  }

  return (
    <div className={rootClassName}>
      {!hideColorGroups && <h5 className={styles.title}>Texture</h5>}
      <div
        className={`${styles.materialTypeScroll} ${hideMaterialTitle ? styles.withCrystals : ''}`}
      >
        {filteredMaterialTypes.map((mt) => (
          <div key={mt.id} className={styles.materialType}>
            {!hideMaterialTitle && (
              <h6 className={styles.materialType__title}>{mt.attributes?.name}</h6>
            )}
            <div className={styles.materials}>
              {filteredMaterials
                .filter((m) => m.attributes?.type?.data?.id === mt.id)
                .map((m) => {
                  if (!hideMaterialTitle) {
                    return (
                      <Material
                        key={m.id}
                        material={m}
                        onMaterialSelected={onMaterialSelected}
                        selectedMaterialId={selectedMaterialId}
                      />
                    );
                  }
                  return (
                    <MaterialCrystal
                      key={m.id}
                      material={m}
                      onMaterialSelected={onMaterialSelected}
                      selectedMaterialId={selectedMaterialId}
                    />
                  );
                })}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};
