import QuickInfo from '@components/ui/QuickInfo/QuickInfo';
import styles from './NavProduction.module.scss';
import { RiQuestionLine } from 'react-icons/ri';
import { useCustomiserStore } from '@store/customiser';

const NavProduction = () => {
  // TODO: items will have dynamic data from the backend
  const { selectedNav, setSelectedNav } = useCustomiserStore();
  const clickHandler = () => {
    if (selectedNav && selectedNav.index) {
      setSelectedNav(selectedNav.index + 1);
    }
  };

  return (
    <div className={styles.root}>
      <QuickInfo>Choose how quickly you need us to work.</QuickInfo>

      <div className={styles.productionCategories}>
        <div className={styles.productionCategories__category} onClick={clickHandler}>
          <div>
            <div className={styles.productionCategories__title}>Standard Production</div>
            <p>Approx 3-4 weeks production + delivery time</p>
          </div>
          <div className={styles.productionCategories__infoIcon}>
            <RiQuestionLine />
          </div>
        </div>
        <div className={styles.productionCategories__category} onClick={clickHandler}>
          <div>
            <div className={styles.productionCategories__title}>Express</div>
            <p>Approx 10-12 days production + delivery time (+ £29)</p>
          </div>
          <span className={styles.productionCategories__infoIcon}>
            <RiQuestionLine />
          </span>
        </div>
        <div className={styles.productionCategories__category} onClick={clickHandler}>
          <div>
            <div className={styles.productionCategories__title}>Super Express</div>
            <p>As fast as 6-8 days production + delivery time (+ £45)</p>
          </div>
          <span className={styles.productionCategories__infoIcon}>
            <RiQuestionLine />
          </span>
        </div>
      </div>
    </div>
  );
};

export default NavProduction;
