import { NavItem } from '@store/customiser';

interface NavItemNameDisplayParameters {
  selectedNav?: NavItem | null;
}
export const navItemNameDisplay = ({ selectedNav }: NavItemNameDisplayParameters) => {
  let navItemName = selectedNav?.name || '';

  if (navItemName.toLowerCase() === 'waist') {
    navItemName = 'Waistband';
  }

  return navItemName;
};

interface OptionModelNameDisplayParameters {
  name?: string | null;
  optionName?: string | null;
}
export const optionModelNameDisplay = ({ name, optionName }: OptionModelNameDisplayParameters) => {
  let modelName = name || '';

  if (optionName?.toLowerCase() === 'waist') {
    switch (modelName.toLowerCase()) {
      case 'waist 4" new':
        modelName = '4"';
        break;
      case 'waist 5" new':
        modelName = '5"';
        break;
      default:
    }
  } else if (optionName?.toLowerCase() === 'leg cut') {
    switch (modelName.toLowerCase()) {
      case 'straight leg':
        modelName = 'Side Slit';
        break;
      default:
    }
  }

  return modelName;
};

interface OptionModelTitleDisplayParameters {
  selectedNav?: NavItem | null;
}
export const optionModelTitleDisplay = ({ selectedNav }: OptionModelTitleDisplayParameters) => {
  let titleDisplay = selectedNav?.name || '';

  if (titleDisplay.toLowerCase() === 'waist' && selectedNav?.type === 'fitting') {
    titleDisplay = 'Waistband Height';
  } else {
    titleDisplay = `Select ${titleDisplay}`;
  }

  return titleDisplay;
};

interface TextTypesDisplayParameters {
  textType?: string | null;
}
export const textTypesDisplay = ({ textType }: TextTypesDisplayParameters) => {
  let display = textType || '';

  if (display.toLowerCase() === 'basic') {
    display = 'Print';
  } else if (display.toLowerCase() === 'luxury') {
    display = 'Embroidery';
  }

  return display;
};
