import QuickInfo from '@components/ui/QuickInfo/QuickInfo';
import styles from './NavBoxxerLogo.module.scss';
import ContainedButton from '@components/ui/Button/ContainedButton';

const NavBoxxerLogo = () => {
  return (
    <div className={styles.root}>
      <QuickInfo>
        To keep our prices reasonable, all products need to include a Boxxer logo. You can drag this
        to where you want it (except small waist band logos on shorts which are in set position). A
        fee applies if you want all logos removed.
      </QuickInfo>
      <ContainedButton widthType='wide' type='hollow'>
        Remove Boxxer Logo + £15
      </ContainedButton>
    </div>
  );
};

export default NavBoxxerLogo;
